body {
  background-color: #141414;
  color: #fff;
  .singlePage {
    .singleHeading {
      background-color: #1e272d;
      padding: 15px 0;
      text-align: center;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;

      h1 {
        text-transform: uppercase;
        font-size: 30px;
      }
      
    }
    .container {
        max-width: 80%;
        margin: auto;
        video {
          width: 100%;
          margin: 50px 0;
        }
        h3 {
          font-weight: 400;
        }
        .para {
          p {
            line-height: 25px;
            margin-top: 20px;
          }
        }
        .soical {
          display: flex;
          justify-content: center;
          align-items: center;
          margin: 30px 0;
          border-top: 1px solid #575757;
          border-bottom: 1px solid #575757;
          padding: 8px 0;
          img{
            margin-left: 10px;
          }
        }
      }
  }
}
