.breadcrumb-parent {
  display: block;
  padding: 9.375em 0 5em;
  z-index: 1;
  text-align: center;
  position: relative;
  background-image: url("../../../public/images/Breadcrumb/bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #191919 !important;
  .container {
    padding: 0 6.25em;
    .row {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      .breadcrumb {
        padding: 2.813em 0;
        position: relative;
        z-index: 2;
        h2 {
          font-size: 3.157em;
          line-height: 1.2;
          letter-spacing: -0.02em;
          font-weight: 400;
          margin: 0;
        }
        .main-breadcrumb {
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          background: transparent;
          padding: 0;
          margin-bottom: 0;
          margin-top: 0.9375em;
          list-style: none;
          border-radius: 0.25em;
          li {
            word-break: break-all;
            display: inline-block;
            a {
              color: #e50914;
              font-weight: 700;
              text-decoration: none;
              outline: medium none !important;
              transition: all 0.5s ease-in-out;
              background-color: transparent;
              cursor: pointer;
            }
          }
          .active {
            position: relative;
            font-weight: 700;
            color: #e50914;
            padding-left: 0.5em;
          }
          i {
            color: #e50914;
            padding-left: 0.5em;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 426px){
    .breadcrumb-parent{
        padding: 3.375em 0 1em !important;
        .container{
            padding: 0 1em !important;
            .row{
                .breadcrumb{
                    h2{
                        font-size: 2em !important;
                    }
                    .main-breadcrumb{
                        flex-wrap: wrap;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {
  .breadcrumb-parent {
    padding: 5.375em 0 3em !important;
    .container {
      padding: 0 1.25em !important;
      .row {
        .breadcrumb {
          h2 {
            font-size: 2.157em !important;
          }
          .main-breadcrumb {
            flex-wrap: wrap;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 1024px) {
  .breadcrumb-parent {
    padding: 5.375em 0 3em;
    .container {
      padding: 0 2.25em;
      .row {
        .breadcrumb {
          h2 {
            font-size: 2.5em;
          }
          .main-breadcrumb {
            flex-wrap: wrap;
          }
        }
      }
    }
  }
}
